body, html, ul, p, h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.page {
  color: #6c788b;
  scroll-behavior: smooth;
  background: #f2f6fa;
  min-width: 320px;
  font-family: Open sans, sans-serif;
  font-size: 14px;
  line-height: 21px;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page__button {
  color: #fff;
  background-color: #2060f6;
  border: 0 solid;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  height: 50px;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 600;
  line-height: 22.5px;
  text-decoration: none;
  transition: transform .3s;
  display: flex;
}

.page__button:hover {
  outline-offset: 15px;
  text-shadow: 1px 1px 2px #427388;
  border: 1px solid;
  transform: scale(1.05);
  box-shadow: inset 0 0 20px #ffffff80, 0 0 20px #fff3;
}

.page__logo {
  width: 46px;
  height: 28px;
  transition: transform .3s;
}

.page__logo:hover {
  transform: scale(1.2);
}

h1, h2, h3 {
  font-family: Poppins, sans-serif;
}

a {
  cursor: pointer;
}

