.navbar__list {
  gap: 48px;
}

.navbar__item {
  display: inline-block;
}

.navbar__link {
  letter-spacing: 3px;
  text-align: center;
  color: #fff;
  padding: 8px 10px;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 19.5px;
  transition: all 1s;
  position: relative;
}

.navbar__link:after, .navbar__link:before {
  content: " ";
  border: 0 solid #2c2c2c;
  width: 10px;
  height: 10px;
  transition: all .4s;
  position: absolute;
}

.navbar__link:after {
  border-top: 3px solid #144ed4;
  border-left: 3px solid #144ed4;
  top: 2px;
  left: 2px;
}

.navbar__link:before {
  border-bottom: 3px solid #144ed4;
  border-right: 3px solid #144ed4;
  bottom: 2px;
  right: 2px;
}

.navbar__link:hover {
  border-top-right-radius: 0;
  border-bottom-left-radius: 0;
}

.navbar__link:hover:before, .navbar__link:hover:after {
  width: 100%;
  height: 100%;
}

.nav {
