  width: 80px;
  height: 20px;
  transition: background-position .3s ease-in-out;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.nav__link span {
  transition: transform .3s ease-in-out;
}

.nav__link:hover span {
  color: #648df9;
  transform: translateX(85px);
}

.nav__link:hover:before {
  background-position: 0;
}

